.container {
  width: 100%;
  display: flex;
  overflow: auto;
  min-height: 100vh;
  align-items: flex-start;
  flex-direction: column;
  justify-content: flex-start;
}
.Features {
  flex: 0 0 auto;
  width: 100%;
  display: flex;
  padding: var(--dl-space-space-doubleunit);
  z-index: 100;
  max-width: 100%;
  align-items: center;
  flex-direction: column;
  justify-content: flex-start;
  background-color: var(--dl-color-gray-black);
}
.Hero {
  width: 100%;
  display: flex;
  padding: 32px;
  max-width: 1400px;
  min-height: 80vh;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.image {
  width: 25rem;
  object-fit: cover;
}
.container1 {
  display: flex;
  margin-left: 32px;
  flex-direction: column;
}
.text01 {
  color: var(--dl-color-gray-white);
  margin-top: 15px;
  text-align: left;
  margin-bottom: 15px;
  padding-right: 48px;
}
@media(max-width: 991px) {
  .Hero {
    padding: 48px;
    flex-direction: column;
  }
  .image {
    order: 2;
  }
  .container1 {
    align-items: center;
    margin-left: 0px;
    margin-right: 0px;
    margin-bottom: 32px;
  }
  .text01 {
    text-align: center;
    padding-left: 64px;
  }
}
@media(max-width: 767px) {
  .text {
    font-size: 40px;
  }
  .Hero {
    padding-left: 32px;
    padding-right: 32px;
  }
  .image {
    width: 80%;
  }
}
@media(max-width: 479px) {
  .text {
    text-align: center;
  }
  .Hero {
    padding-top: 32px;
    padding-left: var(--dl-space-space-unit);
    padding-right: var(--dl-space-space-unit);
    padding-bottom: 32px;
  }
  .container1 {
    margin-bottom: var(--dl-space-space-unit);
  }
  .text01 {
    padding-left: var(--dl-space-space-unit);
    padding-right: var(--dl-space-space-unit);
  }
}
