.HeaderContainer {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: #26282b;
}
.Header {
  flex: 0 0 auto;
  width: 100%;
  display: flex;
  padding: var(--dl-space-space-doubleunit);
  z-index: 100;
  position: relative;
  max-width: 1110px;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  background-color: #26282b;
}
.Logo {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.image {
  object-fit: cover;
}
.Menu {
  display: flex;
  align-self: center;
  align-items: center;
  flex-direction: row;
}
.navlink {
  color: var(--dl-color-gray-white);
  font-style: normal;
  transition: 0.3s;
  font-family: Montserrat;
  font-weight: 700;
  margin-right: var(--dl-space-space-doubleunit);
  text-decoration: none;
}
.navlink:hover {
  color: var(--dl-color-lightblue-default);
}
.navlink1 {
  color: var(--dl-color-gray-white);
  font-style: normal;
  transition: 0.3s;
  font-family: Montserrat;
  font-weight: 700;
  margin-right: var(--dl-space-space-doubleunit);
  text-decoration: none;
}
.navlink1:hover {
  color: var(--dl-color-lightblue-default);
}
.text {
  color: var(--dl-color-gray-white);
  font-style: normal;
  transition: 0.3s;
  font-family: Montserrat;
  font-weight: 700;
  margin-right: var(--dl-space-space-doubleunit);
}
.text:hover {
  color: var(--dl-color-lightblue-default);
}
.text1 {
  color: var(--dl-color-gray-white);
  font-style: normal;
  transition: 0.3s;
  font-family: Montserrat;
  font-weight: 700;
  margin-right: var(--dl-space-space-doubleunit);
}
.text1:hover {
  color: var(--dl-color-lightblue-default);
}
.navlink2 {
  color: var(--dl-color-gray-white);
  font-style: normal;
  transition: 0.3s;
  font-family: Montserrat;
  font-weight: 700;
  margin-right: var(--dl-space-space-doubleunit);
  text-decoration: none;
}
.navlink2:hover {
  color: var(--dl-color-lightblue-default);
}
.container {
  display: flex;
  position: relative;
}
.text2 {
  color: var(--dl-color-gray-white);
  padding: 8px;
  font-style: normal;
  text-align: center;
  transition: 0.3s;
  font-family: Montserrat;
  font-weight: 700;
  border-color: var(--dl-color-gray-900);
  border-style: solid;
  border-width: 2px;
  border-radius: var(--dl-radius-radius-radius6);
}
.text2:hover {
  color: var(--dl-color-lightblue-default);
  border-color: var(--dl-color-gray-900);
  border-width: 3px;
}
.container1 {
  display: flex;
  align-items: center;
  border-color: var(--dl-color-gray-800);
  border-style: dashed;
  border-width: 0px;
  flex-direction: row;
  justify-content: space-between;
}
.BurgerMenu {
  display: none;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.Menu1 {
  color: var(--dl-color-lightblue-default);
  width: 24px;
  height: 24px;
  margin-left: var(--dl-space-space-unit);
}
.MobileMenu {
  top: 0px;
  flex: 0 0 auto;
  left: 0px;
  width: 100%;
  height: 100vh;
  display: none;
  padding: var(--dl-space-space-doubleunit);
  z-index: 101;
  position: absolute;
  align-items: flex-start;
  flex-direction: column;
  background-color: var(--dl-color-gray-white);
}




@media(max-width: 991px) {
  .container1 {
    display: none;
  }
}
@media(max-width: 767px) {
  .Menu {
    display: none;
  }
  .text2 {
    align-self: center;
  }
  .BurgerMenu {
    display: block;
  }
  .Menu1 {
    fill: var(--dl-color-gray-900);
  }
}
@media(max-width: 479px) {
  .container {
    justify-content: center;
  }
  .container1 {
    display: none;
  }
  .Menu1 {
    fill: var(--dl-color-gray-900);
    color: var(--dl-color-lightblue-default);
  }
}
